
.incorporation {
  padding-top: 170px;
  padding-bottom: 100px;
  border-bottom: 2px solid #D6DABD;
  @media screen and (max-width: 768px) {
    padding-top: 130px;
    padding-bottom: 60px;
  }

  &__container {
    width: 100%;
    max-width: 840px;
    margin: 0 auto;
    padding: 0 20px;
  }

  &__container-item {
    .headline {
      padding: 16px 52px 16px 24px;
      background-color: #F8C945;
      border: 2px solid #000000;
      border-radius: 12px;
      position: relative;
      font-size: 24px;
      line-height: 120%;
      margin-bottom: 20px;
      cursor: pointer;
      @media screen and (max-width: 768px) {
        font-size: 18px;
      }

      &:after {
        content: '';
        width: 18px;
        height: 9px;
        display: block;
        background-image: url("../img/arrow.svg");
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
        position: absolute;
        top: 50%;
        right: 24px;
        transform: translateY(-50%) rotateZ(-180deg);
        transition: .3s all;
      }
    }

    .description {
      font-size: 16px;
      line-height: 26px;
      max-height: 0;
      overflow: hidden;
      padding-bottom: 0;
      transition: .2s max-height, .2s padding-bottom;
      @media screen and (max-width: 768px) {
        font-size: 12px;
      }

      &__list {
        padding-left: 20px;
      }

      &__list-item {
        list-style: disc;
        margin-bottom: 8px;

        &.accent {
          color: #F21054;
        }
      }

      &__title {
        font-family: MabryMedium;
        font-size: 24px;
        line-height: 120%;
        margin-bottom: 8px;
        @media screen and (max-width: 768px) {
          font-size: 16px;
        }
      }

      &__content {
        margin-bottom: 8px;
      }

      &__content-line {
        font-family: MabryBlack;
        color: #F21054;
      }
    }

    &.active {
      .headline {
        &:after {
          transform: translateY(-50%) rotateZ(0deg);
        }
      }

      .description {
        padding-bottom: 20px;
        max-height: 2500px;
        //max-height: max-content;
      }
    }
  }

  &__table {
    border-spacing: 0;
    width: 100%;
    border-radius: 4px;
    border: 1px solid #B9B9B9;

    th {
      min-height: 40px;
      font-family: MabryBlack;
      font-size: 14px;
      line-height: 16px;
      text-align: left;
      background: rgba(12, 23, 25, 0.06);
      @media screen and (max-width: 768px){
        font-size: 12px;
        line-height: 16px;
      }
    }
    tr:last-child {
      td {
        border-bottom: none;
      }
    }

    td:last-child,
    th:last-child {
      border-bottom: 1px solid #B9B9B9;
      border-right: none;
    }

    td {
      border-bottom: 1px solid #B9B9B9;
      min-height: 38px;
      font-size: 14px;
      line-height: 18px;
      vertical-align: top;
      @media screen and (max-width: 768px){
        font-size: 12px;
        line-height: 16px;
      }
      li {
        padding-left: 12px;
        list-style: disc;
        margin-bottom: 8px;
        @media screen and (max-width: 768px){
          padding-left: 10px;
          margin-bottom: 6px;
        }
      }
      p {
        margin-bottom: 16px;
        @media screen and (max-width: 768px){
          margin-bottom: 8px;
        }
      }
    }

    td:first-child {
      background: rgba(12, 23, 25, 0.06);
      font-family: MabryBlack;
    }

    th,
    td {
      padding: 10px 12px;
      width: 41%;
      border-right: 1px solid #B9B9B9;
      border-bottom: 1px solid #B9B9B9;
      @media screen and (max-width: 768px){
        padding: 10px 6px;
      }
      @media screen and (max-width: 340px){
        padding: 10px 2px;
      }
    }

    td:first-child,
    th:first-child {
      width: 18%;
    }
  }
}
