.footer {
  padding: 80px 0;
  max-width: 1240px;
  margin: 0 auto;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  flex-wrap: wrap;
  @media screen and (max-width: 1919px) {
    max-width: 1060px;
  }
  @media screen and (max-width: 1199px) {
    max-width: 600px;
  }
  @media screen and (max-width: 767px) {
    flex-wrap: nowrap;
    flex-direction: column;
    align-items: center;
  }

  &__left {
    width: 100%;
    max-width: 500px;
    @media screen and (max-width: 1919px) {
      max-width: 380px;
    }
    @media screen and (max-width: 1199px) {
      max-width: 270px;
    }
    @media screen and (max-width: 767px) {
      text-align: center;
    }
  }

  &__left-logo {
    font-weight: 700;
    font-size: 62.3505px;
    line-height: 140%;
    color: #000000;
    margin-bottom: 36px;
  }

  &__left-content {
    font-weight: 500;
    font-size: 32px;
    line-height: 124%;
    color: #000000;
    margin-bottom: 32px;
    @media screen and (max-width: 1919px) {
      font-size: 24px;
      margin-top: 36px;
    }
    @media screen and (max-width: 1199px) {
      margin-bottom: 36px;
      order: 1;
    }
    @media screen and (max-width: 767px) {
      width: 225px;
      margin: 25px auto;
      font-size: 20px;
    }
  }

  .btn {
    @media screen and (max-width: 767px) {
      margin: 0 auto;
    }
  }

  &__center {
    width: 100%;
    max-width: 240px;
    @media screen and (max-width: 1919px) {
      max-width: 225px;
    }
    @media screen and (max-width: 1199px) {
      margin-bottom: 60px;
      margin-top: auto;
      order: 2;
    }
    @media screen and (max-width: 767px) {
      margin-top: 65px;
      text-align: center;
    }
  }

  &__right {
    width: 100%;
    max-width: 290px;
    @media screen and (max-width: 1919px) {
      max-width: 225px;
    }
    @media screen and (max-width: 1199px) {
      margin-left: auto;
      order: 4;
    }
    @media screen and (max-width: 767px) {
      text-align: center;
      margin: 0;
      order: 3;
    }
  }

  .headline {
    font-weight: 700;
    font-size: 32px;
    line-height: 140%;
    color: #000000;
    margin-bottom: 39px;
    @media screen and (max-width: 1919px) {
      font-size: 24px;
    }
    @media screen and (max-width: 1199px) {
      margin-bottom: 24px;
    }
  }

  .menu {
    &__item {
      margin-bottom: 23px;
      @media screen and (max-width: 1919px) {
        margin-bottom: 16px;
      }
      @media screen and (max-width: 767px) {
        margin-bottom: 8px;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }

    &__item-link {
      font-weight: 500;
      font-size: 32px;
      line-height: 140%;
      color: #000000;
      @media screen and (max-width: 1919px) {
        font-size: 24px;
      }
    }
  }

  &__copyright {
    margin-top: 72px;
    width: 100%;
    @media screen and (max-width: 1919px) {
      margin-top: 64px;
    }
    @media screen and (max-width: 1199px) {
      width: fit-content;
      margin-top: 0;
      margin-bottom: 60px;
      order: 3;
    }
    @media screen and (max-width: 767px) {
      margin-top: 64px;
      margin-bottom: 0;
      order: 4;
    }
  }
}
