@import "colors";
@import "header";
@import "incorporation";
@import "footer";

@font-face {
  font-family: Mabry;
  src: url(../fonts/mabry-regular-pro.ttf);
  font-style: normal;
}

@font-face {
  font-family: MabryMedium;
  src: url(../fonts/Mabry-Pro-Medium.ttf);
  font-style: normal;
}

@font-face {
  font-family: MabryBlack;
  src: url(../fonts/Mabry-Pro-Black.ttf);
  font-style: normal;
}

html {
  scroll-behavior: smooth;
  overflow-x: hidden;
}

body {
  font-family: 'Mabry', sans-serif;
  background-color: $main-bg;
  //min-height: 1000px;
}

* {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, p, ul, li {
  margin: 0;
  padding: 0;
}

.container {
  width: 100%;
  max-width: 2560px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

ul, li {
  padding: 0;
  margin: 0;
}

main {
  min-height: calc(100vh - 521px);
}

strong {
  font-family: MabryMedium;
}

.btn {
  display: block;
  width: max-content;
  padding: 24px 32px;
  background-color: $btn;
  border: 2px solid $main-text;
  box-shadow: 0 6px 0 $btn-box-shadow;
  border-radius: 16px;
  text-transform: uppercase;
  font-family: 'MabryMedium', sans-serif;
  font-size: 20px;
  color: $main-text;
  line-height: 20px;
  transition: .3s all;

  &:hover {
    background-color: $btn-hover;
  }

  @media screen and (max-width: 767px) {
    width: 155px;
    height: 55px;
    font-size: 16px;
    padding: 18px 15px;
    text-align: center;
  }
}


.main {
  position: relative;

  &__block-content {
    height: 920px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    z-index: 100;
    @media screen and (max-width: 1919px) {
      height: 736px;
    }
    @media screen and (max-width: 767px) {
      height: 780px;
      justify-content: start;
      padding-top: 150px;
    }

    .headline {
      width: 100%;
      max-width: 585px;
      font-size: 64px;
      line-height: 92%;
      text-align: center;
      margin-bottom: 28px;
      @media screen and (max-width: 1919px) {
        font-size: 48px;
        margin-bottom: 20px;
      }
      @media screen and (max-width: 767px) {
        max-width: 300px;
        font-size: 32px;
      }
    }

    .description {
      width: 100%;
      max-width: 460px;
      font-size: 20px;
      line-height: 140%;
      text-align: center;
      margin-bottom: 28px;
      @media screen and (max-width: 1919px) {
        font-size: 18px;
        margin-bottom: 20px;
      }
      @media screen and (max-width: 1199px) {
        font-size: 16px;
      }
      @media screen and (max-width: 767px) {
        max-width: 340px;
        font-size: 18px;
      }
    }
  }

  &__block-bg {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 2560px;
    height: 920px;
    background-image: url("../img/bg-main.jpg");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    overflow: hidden;
    @media screen and (max-width: 1919px) {
      width: 100%;
      height: 100%;
      &:before {
        content: '';
        position: absolute;
        background-image: url("../img/bg-decor-left.svg");
        background-size: contain;
        background-repeat: no-repeat;
        width: 615px;
        height: 425px;
        left: 0;
        bottom: 0;
        z-index: 10;
        @media screen and (max-width: 1400px) {
          width: 500px;
          height: 350px;
        }
        @media screen and (max-width: 1199px) {
          width: 630px;
          height: 440px;
          left: -310px;
        }
        @media screen and (max-width: 767px) {
          width: calc(100vw);
          height: calc(100vw * 0.7);
          left: 0;
          transform: translateX(-50%);
        }
      }

      &:after {
        content: '';
        position: absolute;
        background-image: url("../img/bg-decor-right.svg");
        background-size: contain;
        background-repeat: no-repeat;
        width: 710px;
        height: 620px;
        right: 0;
        bottom: 0;
        z-index: 10;
        @media screen and (max-width: 1400px) {
          width: 590px;
          height: 520px;
        }
        @media screen and (max-width: 1199px) {
          right: -170px;
        }
        @media screen and (max-width: 767px) {
          width: 300px;
          height: 280px;
          right: -110px;
        }
      }
    }

    @media screen and (max-width: 767px) {
      background-position: 70%;
    }

    .parallax-block {
      width: 100%;
      height: 100%;
      z-index: 100;
      @media screen and (max-width: 1919px) {
        display: none;
      }
    }

    .decor {
      position: absolute;
    }

    .decor-1 {
      width: 615px;
      height: 626px;
      bottom: -95px;
      left: 580px;
      z-index: 80;

    }

    .decor-2 {
      width: 3300px;
      height: 52px;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      z-index: 60;
      @media screen and (max-width: 1919px) {
        height: 40px;
      }
    }

    .decor-3 {
      bottom: -90px;
      left: 527px;
      width: 283px;
      height: 356px;
      z-index: 80;
    }

    .decor-4 {
      bottom: 215px;
      left: 400px;
      width: 267px;
      height: 272px;
      @media screen and (max-width: 1919px) {
        left: 20px;
        top: 380px;
        width: 218px;
        height: 220px;
      }
      @media screen and (max-width: 1199px) {
        display: none;
      }
      @media screen and (max-width: 767px) {
        display: block;
        left: -130px;
        top: 410px;
      }
    }

    .decor-5 {
      bottom: -195px;
      left: 689px;
      width: 255px;
      height: 303px;
      z-index: 110;

    }

    .decor-6 {
      bottom: -125px;
      left: 400px;
      width: 255px;
      height: 303px;
      z-index: 50;
    }

    .decor-7 {
      bottom: 0;
      right: 633px;
      width: 396px;
      height: 403px;
      z-index: 90;
    }

    .decor-8 {
      bottom: -140px;
      right: 543px;
      width: 283px;
      height: 356px;
      z-index: 120;
    }

    .decor-9 {
      bottom: 0;
      right: 1016px;
      width: 222px;
      height: 101px;
      z-index: 100;
    }

    .decor-10 {
      bottom: 0;
      right: 40px;
      width: 801px;
      height: 790px;
      z-index: 110;
    }

    .decor-11 {
      bottom: 0;
      left: 0;
      width: 2600px;
      height: 706px;
      @media screen and (max-width: 1919px) {
        width: 2198px;
        height: 700px;
        bottom: -80px;
        left: 50%;
        transform: translate(-50%, 0);
      }
      @media screen and (max-width: 767px) {
        width: 1675px;
        height: 700px;
        bottom: 0;
      }
    }

    .decor-12 {
      bottom: 25px;
      left: 1130px;
      width: 66px;
      height: 66px;
    }
  }

}

.partners {
  width: 100%;
  padding: 40px 0;
  min-height: 152px;
  background-color: $main-text;
  @media screen and (max-width: 1199px) {
    min-height: 112px;
  }
  @media screen and (max-width: 767px) {
    min-height: 96px;
  }

  &__marquee {
    display: flex;
    align-items: center;
    width: 100%;
  }

  &__marquee-headline {
    font-weight: 400;
    font-size: 64px;
    letter-spacing: -0.025em;
    color: #FFFFFF;
    display: block;
    white-space: nowrap;
    margin-right: 20px;
    @media screen and (max-width: 1919px) {
      font-size: 48px;
    }
    @media screen and (max-width: 767px) {
      font-size: 32px;
    }
  }

  &__marquee-logo {
    height: 72px;
    margin-right: 20px;
    display: block;
    @media screen and (max-width: 1199px) {
      height: 45px;
    }
    @media screen and (max-width: 767px) {
      height: 35px;
    }
  }
}

.founders {
  padding-top: 130px;
  text-align: center;
  @media screen and (max-width: 1919px) {
    padding-top: 100px;
  }
  @media screen and (max-width: 1199px) {
    padding-top: 80px;
  }

  .headline {
    margin: 0 auto 16px;
    font-weight: 500;
    font-size: 64px;
    line-height: 112%;
    @media screen and (max-width: 1919px) {
      font-size: 48px;
    }
    @media screen and (max-width: 767px) {
      max-width: 340px;
      font-size: 32px;
      br {
        display: none;
      }
    }
  }

  .description {
    font-weight: 500;
    font-size: 42px;
    line-height: 132%;
    margin-bottom: 59px;
    @media screen and (max-width: 1919px) {
      font-size: 32px;
      margin-bottom: 48px;
    }
    @media screen and (max-width: 1199px) {
      font-size: 24px;
    }
    @media screen and (max-width: 767px) {
      max-width: 250px;
      margin: 0 auto 32px;
      font-size: 16px;
    }
  }

  &__card {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media screen and (max-width: 1199px) {
      flex-wrap: wrap;
    }

  }

  &__card-item {
    flex: 1;
    height: 630px;
    border-top: 2px solid #000;
    border-bottom: 2px solid #000;
    border-right: 2px solid #000;
    background-color: #fff;
    background-image: url("../img/card-bg.png");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    padding: 40px;
    position: relative;
    @media screen and (max-width: 1919px) {
      height: 530px;
    }
    @media screen and (max-width: 1199px) {
      flex-basis: 50%;
    }
    @media screen and (max-width: 767px) {
      height: 300px;
      padding: 15px;
    }

    &:after {
      content: '';
      width: 480px;
      height: 550px;
      display: block;
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
      position: absolute;
      top: 0;
      right: 0;
      @media screen and (max-width: 1919px) {
        width: 100%;
        height: 410px;
        background-position: top right;
      }
      @media screen and (max-width: 767px) {
        height: 230px;
      }
    }

    &:before {
      content: '';
      width: 31px;
      height: 31px;
      display: block;
      background-image: url("../img/card-arrow.svg");
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
      position: absolute;
      top: 32px;
      left: 48px;
    }

    &:first-child {
      border-left: 2px solid #000;
    }

    &.item-1 {
      &:after {
        background-image: url("../img/card-1.svg");
      }
    }

    &.item-2 {
      &:after {
        background-image: url("../img/card-2.svg");
      }
    }

    &.item-3 {
      &:after {
        background-image: url("../img/card-3.svg");
      }

      @media screen and (max-width: 1199px) {
        border-left: 2px solid #000;
        border-top: none;
      }
    }

    &.item-4 {
      &:after {
        background-image: url("../img/card-4.svg");
      }

      @media screen and (max-width: 1199px) {
        border-top: none;
      }
    }

    .title {
      width: 100%;
      max-width: 400px;
      margin-bottom: 0;
      font-weight: 500;
      font-size: 56px;
      line-height: 100%;
      text-align: left;
      @media screen and (max-width: 1919px) {
        font-size: 42px;
      }
      @media screen and (max-width: 1400px) {
        font-size: 32px;
      }
      @media screen and (max-width: 1199px) {
        font-size: 42px;
      }
      @media screen and (max-width: 767px) {
        font-size: 24px;
      }
      @media screen and (max-width: 425px) {
        font-size: 18px;
      }
    }
  }

  &__mail {
    width: 50%;
    height: 858px;
    margin: 0 auto;
    padding: 65px 45px;
    text-align: center;
    border-left: 2px solid #000000;
    border-right: 2px solid #000000;
    background-color: #E8F1B0;
    background-image: url("../img/mail-bg.png");
    background-position: top;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    @media screen and (max-width: 1919px) {
      height: 705px;
      padding-top: 40px;
    }
    @media screen and (max-width: 1199px) {
      width: 100%;
      max-width: 690px;
    }
    @media screen and (max-width: 767px) {
      width: 320px;
      height: 590px;
      padding: 65px 20px;
    }

    &:after {
      content: '';
      width: calc(50vw + 200px);
      height: 14.75vw;
      max-height: 379px;
      display: block;
      background-image: url("../img/envelope.svg");
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      z-index: 20;
      @media screen and (max-width: 1199px) {
        width: 750px;
        height: 205px;
      }
      @media screen and (max-width: 767px) {
        width: 344px;
        height: 94px;
      }
    }

    .btn {
      margin: 0 auto;
    }
  }

  &__mail-image {
    width: 200px;
    height: 190px;
    display: block;
    margin: 0 auto 45px;
    @media screen and (max-width: 1919px) {
      width: 150px;
      height: 145px;
      margin-bottom: 40px;
    }
    @media screen and (max-width: 767px) {
      width: 105px;
      height: 100px;
    }
  }

  &__mail-description {
    font-weight: 500;
    font-size: 41px;
    line-height: 132%;
    margin-bottom: 16px;
    @media screen and (max-width: 1919px) {
      font-size: 32px;
    }
    @media screen and (max-width: 767px) {
      font-size: 16px;
    }
  }

  &__mail-title {
    width: 100%;
    max-width: 700px;
    font-weight: 500;
    font-size: 64px;
    line-height: 112%;
    margin: 0 auto 45px;
    @media screen and (max-width: 1919px) {
      font-size: 48px;
      margin-bottom: 40px;
    }
    @media screen and (max-width: 767px) {
      font-size: 32px;
      margin-bottom: 20px;
    }
  }
}

.slider {
  height: 830px;
  background-color: #000;
  margin-top: -70px;
  padding-top: 115px;
  position: relative;
  @media screen and (max-width: 1919px) {
    height: 636px;
  }
  @media screen and (max-width: 1199px) {
    height: 500px;
    padding-top: 110px;
  }
  @media screen and (max-width: 767px) {
    height: 635px;
    padding-top: 0;
    margin-top: -30px;
  }

  &:after {
    content: '';
    width: 1914px;
    height: 1130px;
    display: block;
    background-image: url("../img/decor-slider.png");
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    position: absolute;
    top: -70px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 30;
    @media screen and (max-width: 1919px) {
      width: 1468px;
      height: 880px;
      left: 43%;
    }
    @media screen and (max-width: 1199px) {
      width: 1130px;
      height: 600px;
      left: 40%;
      top: -35px;
    }
    @media screen and (max-width: 767px) {
      width: 414px;
      height: 814px;
      background-image: url("../img/bg-slider.png");
      left: 50%;
    }
  }

  &__founder {
    margin: 0 auto;
    max-width: 1730px;
    position: relative;
    z-index: 40;
    overflow: hidden;
    @media screen and (max-width: 1919px) {
      max-width: 1080px;
    }
    @media screen and (max-width: 1199px) {
      width: 700px;
      overflow: visible;
    }
    @media screen and (max-width: 767px) {
      width: 100%;
    }
  }

  .swiper-wrapper {
    width: 100%;

  }

  .swiper-slide {
    color: #fff;
    height: 600px;
    display: flex;
    align-items: center;
    justify-content: center;
    @media screen and (max-width: 1919px) {
      height: 450px;
    }
    @media screen and (max-width: 1199px) {
      height: 100%;
    }
    @media screen and (max-width: 767px) {
      position: relative;
      flex-direction: column;
      height: 635px;
    }

    &__left {
      width: 450px;
      @media screen and (max-width: 1199px) {
        width: 250px;
      }
    }

    &__left-description {
      font-weight: 500;
      font-size: 24px;
      line-height: 124%;
      letter-spacing: -0.035em;
      color: #FDFDF7;
      margin-bottom: 24px;
      @media screen and (max-width: 1919px) {
        font-size: 20px;
      }
      @media screen and (max-width: 1199px) {
        font-size: 16px;
      }
      @media screen and (max-width: 767px) {
        margin-bottom: 230px;
        text-align: center;
      }
    }

    &__left-headline {
      font-weight: 400;
      font-size: 40px;
      line-height: 124%;
      letter-spacing: -0.035em;
      color: #F8C945;
      margin-bottom: 10px;
      @media screen and (max-width: 1919px) {
        font-size: 32px;
      }
      @media screen and (max-width: 1199px) {
        font-size: 24px;
      }
      @media screen and (max-width: 767px) {
        text-align: center;
      }
    }

    &__left-subtitle {
      @media screen and (max-width: 767px) {
        text-align: center;
      }
    }

    &__right {
      width: 600px;
      @media screen and (max-width: 1919px) {
        width: 450px;
      }
      @media screen and (max-width: 1199px) {
        width: 300px;
      }
      @media screen and (max-width: 767px) {
        position: absolute;
        width: 200px;
        top: 230px;
      }
    }

    &__right-image {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  .swiper-button-next,
  .swiper-button-prev {
    @media screen and (max-width: 767px) {
      top: auto;
      bottom: 25px;
      z-index: 15;
    }

    &:after {
      content: '';
      width: 47px;
      height: 30px;
      display: block;
      background-image: url("../img/arrow_left.svg");
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
    }
  }

  .swiper-button-prev {
    @media screen and (max-width: 767px) {
      left: 60px;
    }
  }

  .swiper-button-next {
    @media screen and (max-width: 767px) {
      right: 60px;
    }

    &:after {
      transform: rotateZ(180deg);
    }
  }

  .swiper-pagination-bullet {
    width: 16px;
    height: 16px;
    border: 2px solid #fff;
    background-color: transparent;
    opacity: 1;
    @media screen and (max-width: 767px) {
      width: 12px;
      height: 12px;
    }

    &.swiper-pagination-bullet-active {
      background-color: #fff;
    }
  }

  .swiper-horizontal > .swiper-pagination-bullets, .swiper-pagination-bullets.swiper-pagination-horizontal, .swiper-pagination-custom, .swiper-pagination-fraction {
    @media screen and (max-width: 1199px) {
      bottom: -50px;
    }
    @media screen and (max-width: 767px) {
      bottom: 40px;
    }
  }
}

.line {
  padding: 40px 0;
  min-height: 152px;
  background: linear-gradient(0deg, #F8C945, #F8C945), #FFFFFF;
  @media screen and (max-width: 1199px) {
    min-height: 112px;
    padding: 32px 0;
  }
  @media screen and (max-width: 767px) {
    min-height: 96px;
  }

  &__content {
    font-weight: 500;
    font-size: 73px;
    line-height: 100%;
    letter-spacing: -0.035em;
    text-transform: uppercase;
    color: #000000;
    @media screen and (max-width: 1199px) {
      font-size: 48px;
    }
    @media screen and (max-width: 767px) {
      font-size: 32px;
    }

    span {
      margin: 0 25px;
    }
  }
}

