
.header {
  width: 100%;
  max-width: 864px;
  min-height: 68px;
  padding: 15px 32px;
  border-radius: 16px;
  background-color: #000000;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  top: 33px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 200;
  transition: .5s top;
  @media screen and (max-width: 1199px) {
    max-width: 730px;
  }
  @media screen and (max-width: 767px) {
    max-width: 300px;
    padding: 20px;
    flex-direction: column;
    align-items: start;
  }

  &.bg-toggle {
    background-color: #FFF;
    transition: .3s;
  }

  &__logo {
    font-weight: 700;
    font-size: 24px;
    line-height: 140%;
    color: #FFFFFF;
    transition: .3s;

    &.active {
      color: #000;
    }

    @media screen and (max-width: 767px) {
      margin-left: 20px;
    }
  }

  &__nav {
    @media screen and (max-width: 1199px) {
      margin-left: auto;
    }
    @media screen and (max-width: 767px) {
      display: none;
    }
  }

  &__nav-menu {
    display: flex;
    justify-content: space-between;
    width: 240px;
    @media screen and (max-width: 1199px) {
      width: fit-content;
    }
    @media screen and (max-width: 767px) {

    }

    .menu {
      &__link {
        font-weight: 500;
        font-size: 16px;
        line-height: 140%;
        color: #FFFFFF;
        display: flex;
        align-items: center;
        @media screen and (max-width: 1199px) {
          font-size: 14px;
        }
      }
    }
  }

  &__link {
    font-weight: 500;
    font-size: 16px;
    line-height: 140%;
    color: #F8C945;
    display: flex;
    align-items: center;
    @media screen and (max-width: 1199px) {
      font-size: 14px;
    }
    @media screen and (max-width: 767px) {
      display: none;
    }

    .header-decor.header-decor {
      margin-right: 0;
      @media screen and (max-width: 1199px) {
        margin: 0 14px;
      }
    }

    &:last-child {
      margin-left: -30px;
      @media screen and (max-width: 1199px) {
        margin-left: 0;
      }
    }
  }

  .header-decor {
    width: 8px;
    height: 8px;
    display: block;
    border-radius: 50%;
    background-color: #F8C945;
    margin: 0 22px;
    @media screen and (max-width: 1199px) {
      margin: 0 14px;
    }
  }

  &.active {
    top: -100px;
  }

  &__mobile {
    display: none;
    @media screen and (max-width: 767px) {
      display: flex;
      flex-direction: column;
      width: 100%;
      max-height: 0;
      overflow: hidden;
      transition: .3s;
      &.active {
        max-height: 600px;
      }
    }
  }

  &__mobile-menu {
    display: flex;
    flex-direction: column;
    margin-top: 30px;
  }

  .menu {
    &__mobile-link {
      display: flex;
      align-items: center;
      width: 100%;
      height: 68px;
      padding: 15px 18px;
      margin-bottom: 18px;
      border-radius: 8px;
      font-size: 24px;
      background-color: #EDF8AC;
      color: #000;
    }
  }

  &__mobile-link {
    display: flex;
    align-items: center;
    width: 100%;
    height: 68px;
    padding: 15px;
    margin-bottom: 18px;
    border-radius: 8px;
    font-size: 24px;
    background-color: #F8C945;
    color: #000;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .menu-btn {
    display: none;
    position: absolute;
    width: 30px;
    height: 30px;
    right: 40px;
    outline: none;
    border: none;
    padding: 0;
    background-color: transparent;
    cursor: pointer;
    transition: .3s;

    &.active {
      &:before {
        top: 15px;
        left: 4px;
        transform: rotate(45deg);
        background-color: #000;
      }

      &:after {
        bottom: 15px;
        left: 4px;
        transform: rotate(-45deg);
        background-color: #000;
      }
    }

    @media screen and (max-width: 767px) {
      display: block;
    }

    &:before,
    &:after {
      content: '';
      position: absolute;
      width: 24px;
      height: 1px;
      border-radius: 2px;
      left: 50%;
      transform: translate(-50%, 0);
      background-color: #FFF;
      transition: .3s;
    }

    &:before {
      top: 11px;
    }

    &:after {
      bottom: 11px;
    }
  }
}
